
let menu = document.querySelector('.menu')
let navegacion = document.querySelector('.navegacion')
let close = document.querySelector('.close')

/* Abrir menu */

menu.addEventListener('click', () => {
    navegacion.classList.add('activo')
    close.classList.add('activo')
})

/* Quitar menu */

close.addEventListener('click', () => {
    navegacion.classList.remove('activo')
    close.classList.remove('activo')
})

/* Acordeon */
let acorderon = document.querySelector('.acordeon')
acorderon.addEventListener('click', () => {
    const contenidoAcord = document.querySelector('.cont_acordeon')
    contenidoAcord.classList.toggle('show')
    acorderon.classList.toggle('menos')
})
let acorderon1 = document.querySelector('.acordeon1')
acorderon1.addEventListener('click', () => {
    const contenidoAcord = document.querySelector('.cont_acordeon1')
    contenidoAcord.classList.toggle('show')
    acorderon1.classList.toggle('menos')
})

/* Efecto de scroll */
window.addEventListener('scroll', () => {
    let barra = document.querySelector('.header')
    barra.classList.toggle('sticky', scrollY > 0)

    let goTop = document.querySelector('.go-top')
    goTop.classList.toggle('showArrow', scrollY > 500 )

    goTop.addEventListener('click', () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
    })

})

/*  */
function efectoScroll(){
    const barra = document.querySelectorAll('.navegacion a')
    barra.forEach( function (enlace){
        enlace.addEventListener('click', function(e){
            e.preventDefault()

            navegacion.classList.remove('activo')
            close.classList.remove('activo')
            const seleccionado = document.querySelector(e.target.attributes.href.value)
            
            seleccionado.scrollIntoView({
                behavior: 'smooth'
            })


        })
    })
}
efectoScroll()
